<template>
  <div>
    <search-bar @onSearch="handleSerch"></search-bar>
      <div class="content">
          <el-menu
                  class="categories"
                  :default-active="activeIndex"
                  @select="handleSelect"
                  active-text-color="red">
              <el-menu-item index="0"  >
                  <i class="el-icon-menu"></i>
                  <span slot="title">全部</span>
              </el-menu-item>
              <el-menu-item :index="item.id" v-for="item in menuItem" :key="item.title">
                  <i class="el-icon-menu"></i>
                  <span slot="title">{{item.title}}</span>
              </el-menu-item>
          </el-menu>
          <div class="movie-list">
              <el-row>
                  <el-col :span="6" v-for="(item, index) in filterList" :key="item.vid" >
                      <router-link :to="`/movieDetail/${item.vid}`">
                          <el-card :body-style="{ padding: '0px' }">
                              <img :src="item.url" class="image">
                              <div style="padding: 0px 8px 15px 8px;">
                                  <div style="font-size: 18px;margin-bottom:10px">{{item.title}}</div>
                                  <div class="content-detail" >{{item.content}}</div>
                              </div>
                          </el-card>
                      </router-link>

                  </el-col>
              </el-row>
          </div>
      </div>
  </div>
</template>
<script>

  import SearchBar from './SearchBar'
  import bg2 from '../../assets/bg2.jpg'
  const axios = require('axios')
  export default {
    components: {SearchBar},
    data () {
      return {
        activeIndex:'0',
        active:'0',
        menuItem: [{
          id: '1',
          title: '动漫'
        },{
          id: '2',
          title: '电影'
        },{
          id: '3',
          title: '电视剧'
        },
        ],
        movieList:[],
        filterList:[]
      }
    },
    created (){
      this.getVideo()
    },
    methods: {
      //获取视频列表
      getVideo () {
        axios({
          method: 'post',
          url: '/index?action=getvideo',
          data: {vid: 1002},
          header: {
            // 如果写成contentType会报错
            'Content-Type': 'application/json'
          }
        }).then(resp => {
          //服务端返回一个视频数组
          if (!resp.data.videos) {
            this.$alert('获取信息失败！', '提示', {
              confirmButtonText: '确定'
            })
          } else {
            let videos = resp.data.videos
            for(let item of videos) {
              this.movieList.unshift({
                vid: item.vid,
                menuId: item.tid,
                title: item.title,
                content: item.text,
                url: 'https://shadow.elemecdn.com/app/element/hamburger.9cf7b091-55e9-11e9-a976-7f4d0b07eef6.png',
                videoSrc: item.video
              })
            }
            this.filterList = this.movieList
            //var storage=window.localStorage
            var storage=window.sessionStorage
            storage.setItem('movieList',JSON.stringify(this.movieList));
          }

        }).catch(failResponse => {
        })
      },

      handleSelect (key) {
        this.active = key;
        if (key == 0) {
          this.filterList = this.movieList;
        } else {
          this.filterList = this.movieList.filter((item) => {
            return item.menuId == key
          })
        }
      },

      //处理搜索函数
      handleSerch (keywords) {
        //连接服务器进行通信
        axios({
          method: 'post',
          url: '/index?action=search',
          data: {
            'keywords': keywords
          },
          header: {
            // 如果写成contentType会报错
            'Content-Type': 'application/json'
          }
        }).then(resp => {
          //接收服务端返回的数据，都是数组，一个是用户组，一个是影片组
          let authors = resp.data.author
          let videos = resp.data.video
          //获取数据，要遍历
          alert(authors[0].uid)
        }).catch(failResponse => {})
      }
    }
  }
</script>

<style>
  .nav-menu {
    /*margin-bottom: 40px;*/
    box-shadow: 0 2px 4px 8px rgba(0,0,0,.05);
  }
  .content{
      width: 1200px;
      margin: 30px auto;
      position: relative;
  }
  .categories {
      position: fixed;
      top: 123px;
      width: 150px;
  }
    .movie-list{
        margin-left: 180px;
    }
    .movie-list a{
        color: #999;
        text-decoration: none;
    }
  .el-card{
      background: transparent;
      padding-top: 10px;

  }
  .image{
      width: 235px;
      height: 235px;
  }
    .content-detail{
        text-align: left;word-break: break-all;

        overflow: hidden;
        text-overflow:ellipsis;
        white-space: nowrap;
        color: #999;

    }
</style>



